import React from 'react';
import ReactDOM from 'react-dom';
import $ from 'jquery';
import './LoginForm.css';


class LoginForm extends React.Component{
	constructor(props){
		super(props);
		this.state ={
			captcha: formula(),
			timeOut: 8,
			captchaAnswer: "",
			captchaVerify: null,
			accountVerify: null
		}
		this.timer = null;
	}
	
	componentDidMount(){
		var element = $(ReactDOM.findDOMNode(this));
		setTimeout(()=>{
			element.removeClass("hide");
		},200);
		this.timer=setInterval(()=>this.tick(), 1000);
	}
	
	componentWillUnmount(){
		clearInterval(this.timer);
	}
	
	/*------------	Captcha Part --------------*/
	tick(){
		this.setState((prevState)=>{
			if(prevState.timeOut===1){
				return {
					captcha: formula(),
					timeOut: 8,
					captchaAnswer: "",
					captchaVerify: null
				}
			}
			else
				return {
					timeOut: prevState.timeOut - 1
				}
		})
	}
	
	userInputCaptchaResult(event){
		this.setState({
			captchaAnswer: event.target.value
		})
	}
	
	userFinishCaptchaResult(event){
		var userResult = this.state.captchaAnswer;
		var trueResult = this.state.captcha[1];
		if(userResult===trueResult){
			this.setState({
				captchaVerify: true
			},()=>{
				clearInterval(this.timer);
				setTimeout(function(){
					$("#captchaSuccess").removeClass("hide");
				},200);
			});
		}
		else{
			this.setState({
				captchaVerify: false
			},()=>{
				setTimeout(function(){
					$("#captchaErrorText").removeClass("hide");
				},200);
			});
		}
	}
	
	/*------------	Login Part --------------*/
	loginToAccount(){
		var URL = this.props.ServerAddress+"/loginToAccount";
		var data = {
			emailAddress: $("#accountLoginEmail").val(),
			password: $("#accountLoginPIN").val()
		};
		fetch(URL,{
			body: JSON.stringify(data),			
			cache: 'no-cache',
			headers: { 'Accept': 'application/json', 'Content-Type': 'application/json' },
			method: 'POST',
		}).then(response => {
  			return response.json();					// parses response to JSON
		}).then(jsonResponse => {
  			this.setState({
				accountVerify: jsonResponse.result
			},()=>{
				if(this.state.accountVerify===true)
					window.location.href = this.props.ServerAddress+"/userHomePage";
			});
		}).catch((error)=>{
			console.log(error);
		});
	}
	
	render(){
		var captchaField;
		var captchaError;
		var captchaErrorText = null;
		var submitButton = null;
		if(this.state.captchaVerify!==true){
			captchaError = this.state.captchaVerify===false?"captchaError":"";
			if(this.state.captchaVerify===false){
				captchaErrorText = <div id="captchaErrorText" className="form-group row opacityAnimate hide">
													<div className="col-5 offset-6">
														<p className="captchaErrorText">Sorry, the answer is wrong!</p>
													</div>
												</div>
			}
			captchaField = <div>
										<div className="form-group row">
											<div className="col-10 offset-1">
												<p id="accountLoginCaptchaIndicator">Please answer below question (Refresh in <span>{this.state.timeOut}</span> sec)</p>
											</div>
										</div>
										<div className="form-group row">
											<label className="col-1 offset-1 col-form-label">
												<i className="fas fa-question-circle"></i>
											</label>
											<label id="captchaQuestion" htmlFor="accountLoginBotCheck" className="col-4 col-form-label d-flex justify-content-between">
												<span>{this.state.captcha[0]}</span>
											</label>
											<div className="col-5">
												<input id="accountLoginBotCheck" type="text" className={'form-control '+captchaError} 
													value={this.state.captchaAnswer}
													onChange={(event)=>this.userInputCaptchaResult(event)}
													onBlur={()=>this.userFinishCaptchaResult()}
													placeholder="Your Answer..."/>
											</div>
										</div>
										{captchaErrorText}
									</div>
			submitButton = <button type="button" className="btn btn-block" disabled>Not Available...</button>
		}
		else{
			captchaField= <div id="captchaSuccess" className="form-group row opacityAnimate hide">
										<div className="col-10 offset-1">
											<p className="captchaSuccess">
												Congratulation! <a><i className="fas fa-hand-peace"></i></a>
											</p>
										</div>
									</div>
			submitButton = <button id="loginButton" type="button" className="btn btn-block"
							   			onClick={()=>this.loginToAccount()}
							   		  >Log into Homepage</button>
		}
		return	<div id="loginAccount" className="card text-center w-75 opacityAnimate hide">
						<div className="card-header">
    						<h3 className="mb-0">Account Login</h3>
  						</div>
  						<div className="card-body">
							<form id="accountLoginForm" method="post" noValidate>
								<div className="form-group row">
									<label htmlFor="accountLoginEmail" className="col-1 offset-1 col-form-label"><i className="fas fa-envelope-square"></i></label>
									<div className="col-9">
										<input id="accountLoginEmail" type="email" className="form-control" placeholder="Your Email..."/>
									</div>
								</div>
								<div className="form-group row">
									<label htmlFor="accountLoginPIN" className="col-1 offset-1 col-form-label"><i className="fas fa-key"></i></label>
									<div className="col-9">
										<input id="accountLoginPIN" type="password" className="form-control" placeholder="Your Password..."/>
									</div>
								</div>
								{captchaField}
								<div className="form-group row mt-2">
									<div className="col-4 offset-4">
										{submitButton}
									</div>
								</div>
								{this.state.accountVerify===false?(<LoginError/>):null}
								<div className="form-group row mt-3 mb-0">
									<div className="col-4 offset-7 d-flex justify-content-end">
										<a id="wantToCreate" onClick={this.props.goToCreatePage}>
											Don't have one? <i className="fas fa-arrow-circle-right"></i>
										</a>
									</div>
								</div>
							</form>
						</div>
					</div>
	}
}

class LoginError extends React.Component{
	componentDidMount(){
		var element = $(ReactDOM.findDOMNode(this));
		setTimeout(()=>{
			element.removeClass("hide");
		},200);
	}
	render(){
		return	<div className="form-group row opacityAnimate hide">
						<div className="col-10 offset-1">
							<p className="loginErrorText">Either your email or password is wrong, please check them</p>
						</div>
					</div>
	}
}

function formula(){
	/* (a?b)?(c?d) */
	var op1 = Math.random() >=0.5? "+":"−";
	var op2 = Math.random() >=0.5? "x":"/";
	var op3 = Math.random() >=0.5? "+":"−";
	var var1= Math.round(Math.random()*8+1);
	var var2 = Math.round(Math.random()*8+1);
	var var3 = Math.round(Math.random()*8+1);
	var var4 = Math.round(Math.random()*8+1);
	var string = `  (${var1} ${op1} ${var2}) ${op2} (${var3} ${op3} ${var4}) =`;
	if(op2==="/"&&op3==="−"&&var3===var4)
		return formula();
	var tempLeft =0, tempRight =0, result =0;
	if(op1==="+")
		tempLeft = var1 + var2
	else
		tempLeft = var1 -  var2
	if(op3==="+")
		tempRight = var3 + var4
	else
		tempRight = var3 -  var4
	if(op2==="x")
		result = tempLeft * tempRight
	else
		result = tempLeft / tempRight
	return [string, result.toString()];
}

export default LoginForm;


