import React from 'react';
import ReactDOM from 'react-dom';
import $ from 'jquery';
import 'bootstrap';												// import 'bootstrap/dist/js/bootstrap'
import 'bootstrap/dist/css/bootstrap.css';		   // import 'bootstrap/scss/bootstrap.scss'
import './index.css';
import CreateNewAccountForm from './CreateNewAccountForm/CreateNewAccountForm';
import EmailValidation from './EmailValidation/EmailValidation';
import LoginForm from './LoginForm/LoginForm';

const ServerAddress = "";			// const, can be different when depoly on different server

class App extends React.Component{
	constructor(props){
		super(props);
		this.state={
			pageType: "create",
			newAccountInfo: {
				username: "KanzakiYui", 
				password: "yuipassword2018", 
				email: "yaopeng.liu@mail.utoronto.ca"
			}
		}
	}
	
	gotoEmailValidation(){
		var account = {
			username: $("#newAccountID").val(),
			password: $("#newAccountPIN").val(),
			email: $("#newAccountEmail").val()
		};
		this.setState({
			pageType: "emailValidation",
			newAccountInfo: account
		});
	}
	
	gotoLoginPage(){
		this.setState({
			pageType: "login"
		});
	}
	
	goToCreatePage(){
		this.setState({
			pageType: "create"
		});
	}
	
	render(){
		var pageType = this.state.pageType;
		var pageElement = null;
		if(pageType==="create")
			pageElement = 	<CreateNewAccountForm
											ServerAddress = {ServerAddress}
											gotoEmailValidation={()=>this.gotoEmailValidation()} 
											gotoLoginPage={()=>this.gotoLoginPage()}
										/>
		else if(pageType==="emailValidation")
			pageElement =  <EmailValidation 
							   				ServerAddress = {ServerAddress}
							   				newAccountInfo={JSON.stringify(this.state.newAccountInfo)}
											gotoLoginPage={()=>this.gotoLoginPage()}
										/>
		else
			pageElement = 	<LoginForm
											ServerAddress = {ServerAddress}
							  				goToCreatePage={()=>this.goToCreatePage()}
							  		  	/>
				
		return <div className="container py-3 d-flex justify-content-center">
					{pageElement}
				  </div>
	}
}



ReactDOM.render(<App/>, document.getElementById('root'));



