import React from 'react';
import ReactDOM from 'react-dom';
import $ from 'jquery';
import './EmailValidation.css';

class EmailValidation extends React.Component{
	constructor(props){
		super(props);
		this.state={
			sent: false,
			remains: 0,
			inputCode: "",
			verified: null
		};
		this.timeOut = 60;
		this.timer= null;
		this.account = JSON.parse(this.props.newAccountInfo);
	}
	componentDidMount(){
		var element = $(ReactDOM.findDOMNode(this));
		setTimeout(()=>{
			element.removeClass("hide");
		},200);
	}
	
	send(){
		this.setState({
			sent: true,
			remains: this.timeOut
		},()=>{
			this.sendEmail();
			this.timer = setInterval(()=>this.tick(), 1000);
		})
	}
	
	tick(){
		this.setState((prevState)=>{
			var remains = prevState.remains;
			if(remains===1){
				clearInterval(this.timer);
				return {
					remains: remains-1,
					sent: false,
					inputCode: "",
					verified: null
				}
			}
			else
				return { remains: remains-1 }
		})
	}	
	
	sendEmail(){
		var URL = this.props.ServerAddress+"/emailCodeRequest";
		var data = {
			emailAddress: this.account.email,
			emailTimeout: this.timeOut
		}
		fetch(URL,{
			body: JSON.stringify(data),			
			cache: 'no-cache',
			headers: { 'Accept': 'application/json', 'Content-Type': 'application/json' },
			method: 'POST',
		}).then(response => {
  			return response.json();					// parses response to JSON
		}).then(jsonResponse => {
  			console.log(jsonResponse);
		}).catch((error)=>{
			console.log(error);
		});
	}
	
	codeInput(event){
		this.setState({
			inputCode: event.target.value
		})
	};
	sendToVerify(){
		var URL = this.props.ServerAddress+"/emailCodeVerification";
		/*	Once verified, automatically create new account in database */
		var data ={
			code: this.state.inputCode,
			emailAddress: this.account.email,
			username: this.account.username,
			password: this.account.password
		}
		fetch(URL,{
			body: JSON.stringify(data),			
			cache: 'no-cache',
			headers: { 'Accept': 'application/json', 'Content-Type': 'application/json' },
			method: 'POST',
		}).then(response => {
  			return response.json();					// parses response to JSON
		}).then(jsonResponse => {
  			this.setState({
				verified: jsonResponse.result
			},()=>{
				if(this.state.verified===false){
					setTimeout(()=>{
						$("#wrongCodeInput").removeClass("hide");
					},200);
				}
				else{
					clearInterval(this.timer);
					setTimeout(()=>{
						$("#rightCodeNotification").addClass("opacityAnimate");
						$("#rightCodeNotification").removeClass("hide");
					},200);
				}	
			});
		}).catch((error)=>{
			console.log(error);
		});
	}
	
	render(){
		var username = this.account.username;
		var email = this.account.email;
		var verifyButton = null;
		var codeInput = null;
		var wrongCode = this.state.verified===null?"":"wrongCode";
		if(this.state.sent===false){
			verifyButton = <button id="emailVerifyButton" type="button" className="btn btn-block" onClick = {()=>this.send()}>
										Click to send verification code
									</button>
			codeInput = <input type="text" className="form-control text-center" value={this.state.inputCode} 
									placeholder="Not available now..." disabled readOnly></input>
		}
		else{
			verifyButton = <button id="emailVerifyButtonDisabled" type="button" className="btn btn-block" disabled>
										Verification code will expire in {this.state.remains} sec 
									</button>
			codeInput = <input type="text" className={'form-control text-center '+wrongCode} value={this.state.inputCode} 
									onChange={(event)=>this.codeInput(event)}
									onBlur={()=>this.sendToVerify()}
									placeholder="Type your code here..." ></input>
		}
		if(this.state.verified!==true){
			var codeResult = <div id="wrongCodeInput" className="form-group row mt-2 opacityAnimate hide">
											<div className="col-8 offset-2">
												<span className="wrongCode">Sorry! The code you entered is not correct!</span> 
											</div>
										</div>
			if(this.state.verified===null)
				codeResult = null
			return <div id="emailValidationCard" className="card w-75 hide opacityAnimate">
  						<div className="card-header text-center">
    						<h3>Email Validation</h3>
  						</div>
  						<div className="card-body">
    						<h4 className="card-title">Dear {username},</h4>
							<p>Your email address is <span>{email}</span></p>
							<p>Click the button below and get the verification code in your mailbox</p>
							<p>Then type the code received in below field to active your account</p>
							<div className="row mt-4">
								<div className="col-8 offset-2">
									{verifyButton}
								</div>
							</div>
							<div className="row mt-3">
								<div className="col-8 offset-2">
									{codeInput}
								</div>
							</div>
							{codeResult}
  						</div>
					</div>
		}
		else{
			return <div id="rightCodeNotification" className="w-75 hide">
							<h3>
								Congratulation! 
								<a className="ml-3"><i className="fas fa-hand-peace"></i></a>	
							</h3>
							<p>You have successfully actived your account!</p>
							<div className="row mt-3">
								<div className="col-4 offset-4">
									<button type="button" className="btn btn-block btn-outline-danger"
										onClick={this.props.gotoLoginPage}
										>Go to Login</button>
								</div>
							</div>
						</div>
		}
	}
}

export default EmailValidation;


